.footer-title {
    font-size: .35rem;
    font-family: "Mitr-SemiBold";
    font-weight: 600;
    line-height: .48rem;
    margin-bottom: .24rem;
}

.footer-avaliable {
    .footer-avaliable-text {
        font-size: .16rem;
    }
    .footer-avaliable-website {
        width: 2.51rem;
        height: .48rem;
        font-size: .18rem;
        font-family: "Mitr-Medium";
        font-weight: 500;
        line-height: .48rem;
        border-radius: 24px;
        border: 1px solid #FAFAFF;
        margin: 0 auto;
        margin-top: .16rem;
        margin-bottom: .24rem;
        img {
            width: .24rem;
            position: relative;
            top: .06rem;
            margin-right: .16rem;
        }
    }
}

.footer-line {
    img {
        height: .48rem;
    }
}

.footer-text {
    font-size: .16rem;
    font-weight: 300;
    color: #F2F2FF;
    line-height: .32rem;
    &.footer-text-one {
        margin: .24rem 0;
    }
    &.footer-text-two {
        margin: .24rem auto;
    }
}

.media-link {
    a {
        margin: 0 .24rem;
    }
    img {
        width: .52rem;
    }
}
