#part3d {
    position: relative;
    background: #FAFAFF;
    border-radius: 0px 0px 1.2rem 1.2rem;
    padding-top: .66rem;
    padding-bottom: .8rem;
    
    .part3d-content {
        width: 100%;
        // height: 1462px;

        .part3d-main {
            box-sizing: border-box;
            padding-top: .8rem;
            text-align: center;
            .main-top {
                width: 5.25rem;
                margin: 0 auto;
                text-align: center;
                position: relative;
                img.icon-360 {
                    height: .36rem;
                    position: absolute;
                    top: -.38rem;
                    left: 50%;
                    margin-left: -.6rem;
                }
                img.model {
                    height: 4rem;
                    margin-top: .14rem;
                }
                .eq-item {
                    position: absolute;
                    &.eq-item01 {
                        left: -.85rem;
                        top: -1rem;
                    }
                    &.eq-item02 {
                        right: -2.02rem;
                        top: 1rem;
                    }
                    &.eq-item03 {
                        left: -2.03rem;
                        bottom: .92rem;
                    }
                }
            }

            .dino {
                margin-top: .28rem;
            }
            
            .main-bottom {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                align-items: center;
                position: relative;
                top: -.26rem;
                padding: 0 .48rem;

                .btn-trading {
                    width: 3.6rem;
                    height: .64rem;
                    line-height: .64rem;
                    background: linear-gradient(135deg, #F2801D 0%, #F24404 100%);
                    box-shadow: 0px 8px 16px 1px rgba(242, 68, 4, 0.20000000298023224);
                    border-radius: .32rem;
                    text-align: center;
                    font-size: .24rem;
                    font-family: "Mitr-Medium";
                    font-weight: 500;
                    color: #FFF8F2;
                    img {
                        position: relative;
                        top: .03rem;
                        margin-left: .08rem;
                    }
                    position: relative;
                    bottom: .29rem;
                }

                .main-bottom-left, .main-bottom-right {
                    display: flex;
                    align-items: center;
                }
                img {
                    &.model {
                        height: 1.6rem;
                    }
                    &.arrow {
                        margin-top: .4rem;
                    }
                    &.arrow-left {
                        margin-right: .2rem;
                    }
                    &.arrow-right {
                        margin-left: .2rem;
                    }
                    
                }
            }
        }
        
    }
}