* {
  margin: 0;
  padding: 0;
}

html {
  width: 100%;
  height: 100%;
  // min-width: 1280px;
}

body {
  width: 100%;
  height: 100%;
  position: relative;
  font-size: .14rem;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

.fr {
  float: right;
}

.fl {
  float: left;
}

.none-select {
  -moz-user-select:none;/*火狐*/
  -webkit-user-select:none;/*webkit浏览器*/
  -ms-user-select:none;/*IE10*/
  -khtml-user-select:none;/*早期浏览器*/
  user-select:none;
}

@font-face{
  font-family: 'Mitr-Light'; 
  src: url('./fonts/Mitr Light.ttf') format('truetype');
}
@font-face{
  font-family: 'Mitr-Medium'; 
  src: url('./fonts/Mitr Medium.ttf') format('truetype');
}
@font-face{
  font-family: 'Mitr-Regular'; 
  src: url('./fonts/Mitr Regular.ttf') format('truetype');
}
@font-face{
  font-family: 'Mitr-SemiBold'; 
  src: url('./fonts/Mitr SemiBold.ttf') format('truetype');
}

body {
  margin: 0;
  font-family: 'Mitr-Regular', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #FFFAF7;
  // background-color: #2231F2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}